.statistics {
  display: flex;
  border-bottom: 1px solid rgba(190, 190, 190, 0.25);
  padding-bottom: 20px;

  @media (max-width: 480px) {
    flex-direction: column;

    .header {
      text-align: center;
    }

    .section {
      width: 100%;
    }

    .col {
      width: 50%;
    }
  }
}

.section {
  width: 210px;
  display: flex;
  flex-wrap: wrap;
}

.header {
  width: 100%;
  height: 30px;
  font-size: 12px;
  font-weight: 700;
  color: #828282;
  text-transform: uppercase;
  justify-content: space-between;
}

.col {
  height: 60px;
  padding-right: 10px;
}

.row {
  height: 30px;
  text-align: center;

  &.value {
    font-size: 20px;
    font-weight: 700;
  }

  &.title > span {
    border-bottom: 1px dotted;
    font-size: 12px;
    cursor: help;
  }
}
