.prepareForClasses {
  display: flex;
  flex-wrap: wrap;
  border-bottom: 1px solid rgba(190, 190, 190, 0.25);
  padding-bottom: 20px;
}

.title {
  width: 17%;
  padding-right: 20px;
  text-align: end;
  font-size: 14px;
}

.text {
  width: 83%;
  margin-top: 4px;
  font-size: 14px;
}

.buttons {
  padding-left: 17%;

  &.nextClass {
    padding-left: 0;
  }
}

@media (max-width: 768px) {
  .title {
    width: 100%;
    margin: 0;
    text-align: left;
  }

  .text {
    width: 100%;
  }

  .buttons {
    padding-left: 0;
  }
}
