.feedback {
  padding: 20px 0;
  border-bottom: 1px solid rgba(190, 190, 190, 0.25);
}

.title {
  font-size: 12px;
  font-weight: 700;
  color: #828282;
  text-transform: uppercase;
}

.content {
  display: flex;
}

.feedbackSection {
  width: 190px;
  margin: 20px 60px 0 0;
}

.indicator {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 340px;
  padding-top: 20px;
}

.indicatorValue {
  font-size: 14px;
}

.indicatorValueUp {
  color: #27ae60;
}

.indicatorValueDown {
  color: #eb5757;
}

.indicatorProgress {
  width: 100%;
  height: 8px;
  background-color: #eb5757;
  border-radius: 30px;
  overflow: hidden;
}

.indicatorBar {
  height: 100%;
  background-color: #27ae60;
}

.indicatorHint {
  position: relative;
  width: 100%;
  background-color: #fff;
  border: 1px solid #bdbdbd;
  font-size: 12px;
  font-weight: 700;
  padding: 7px 20px;
  right: 0;
  margin-top: 10px;
  border-radius: 4px;

  &::before {
    border-style: solid;
    border-width: 0 7px 7px;
    border-color: transparent transparent #bdbdbd;
    bottom: 100%;
    content: '';
    height: 0;
    right: 2px;
    margin-left: -6px;
    position: absolute;
    width: 0;
  }
}

.sectiontitle {
  font-size: 12px;
  font-weight: 700;
  color: #4f4f4f;
  text-transform: uppercase;
}

.feedbackTag {
  display: flex;
  margin-top: 7px;
  font-size: 12px;

  .value {
    font-weight: 700;
  }

  .title {
    margin-left: 15px;
  }
}

@media (max-width: 768px) {
  .content {
    flex-wrap: wrap;
  }

  .feedbackSection {
    width: auto;
  }

  .indicatorContainer {
    width: 100%;
  }
}

.title2 {
  background: red;
}
