.profileInformation {
  display: flex;
  justify-content: flex-start;
  border-bottom: 1px solid rgba(190, 190, 190, 0.25);
  padding-bottom: 20px;

  @media (max-width: 480px) {
    flex-direction: column;

    .imgContainer {
      order: 0;
      margin-left: 0;
    }
  }
}

.imgContainer {
  display: block;
  min-width: 96px;
  height: 96px;
  float: right;
  position: relative;
  background: #fff;
  border: 0;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 15px;

  @media (max-width: 992px) {
    margin-right: 0;
    margin-left: 15px;
    order: 1;
  }
}

.img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.name {
  font-size: 18px;
  font-weight: normal;
  margin: 10px 0 15px;
}

.line {
  margin-bottom: 5px;
}
