.classField {
  display: flex;
  margin-bottom: 15px;

  @media (max-width: 768px) {
    flex-wrap: wrap;

    .title {
      width: 100%;
    }

    .content {
      padding-top: 15px;
    }
  }
}

.title {
  min-width: 130px;
  padding-top: 5px;
  color: #a4a4a4;
  font-size: 12px;
  text-align: left;
  text-transform: uppercase;
  font-weight: 700;
}
