.completedClass {
  margin-bottom: 40px;
}

.title {
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 700;
}

.status {
  display: inline-block;
  padding: 6px 9px;
  margin-left: 4px;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.25em;
  background-color: #f0ad4e;

  &.completed {
    background-color: #5cb85c;
  }
}

.dataLink {
  display: inline-flex;
  margin-right: 10px;
  padding: 5px 12px;
  border: 1px solid #d4d4d4;
  border-radius: 50px;
  background-color: #f5f5f1;

  &:hover {
    background-color: white;
  }
}

.credit {
  color: #31708f;
  font-size: 75%;
  font-weight: 700;
}

.processing {
  font-size: 75%;
  font-weight: 700;
  color: #8a6d3b;
}

.block {
  margin-bottom: 10px;
}
